@import url(https://fonts.googleapis.com/css2?family=Aclonica&family=Luckiest+Guy&family=RocknRoll+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue','Kaisei HarunoUmi','Aclonica',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



:root{
  --mainBlue:#000000;
  --lightBlue:#ffffff;
  --mainWhite:#f3f3f3;
  --mainDark:#232528;
  --mainYellow:#ffa440;
}
body{
  font-family: 'RocknRoll One', sans-serif !important;
  background: #f3f3f3 !important;
  background: var(--mainWhite) !important;
  color:#232528 !important;
  color:var(--mainDark) !important;
}
.text-title{
  font-family: 'Luckiest Guy', cursive;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
}
.text-blue{
  color: #232528;
  color: var(--mainDark);
}
.text-bright{
  color:#ffffff;
  color:var(--lightBlue);
}

.display-3{
  font-family: 'Aclonica', sans-serif;
}

.btn-black{
  background :transparent;
  text-transform:capitalize;
  font-size: 0.8rem !important;
  color: #232528;
  color: var(--mainDark);
  border-radius: 0 !important;
  border:0.1rem solid #232528 !important;
  border:0.1rem solid var(--mainDark) !important;
}

.btn-black:hover{
  background: #232528 !important;
  background: var(--mainDark) !important;
  color:#f3f3f3 !important ;
  color:var(--mainWhite) !important ;

}

.cart-icon{
  cursor:pointer;
  font-size: 1.2rem;
  color:#ffa440;
  color:var(--mainYellow);
}

.fade-in {
  -webkit-animation: fadeIn ease 5s;
          animation: fadeIn ease 5s;
  
}

@-webkit-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}




